<template>
  <div class="myPoints">
    <header>
      <p>我的积分</p>
      <h1 v-if="list.pay_points">{{list.pay_points+'.00'}}</h1>
      <p>商城下单时可用奖金进行抵扣</p>
    </header>
    <main>
      <h2>积分明细</h2>
      <ul>
        <li v-for="item in list.list" :key="item.log_id">
          <div class="left">
            <h1>{{item.desc}}</h1>
            <p>{{item.change_time | formate}}</p>
          </div>
          <div class="right">{{item.pay_points}}</div>
        </li>
      </ul>
    </main>
  </div>
</template>

<script>
import axios from "axios";
import url from "@/axios/api.js";
export default {
  data() {
    return {
      list: []
    };
  },
  created() {
    this.getData();
  },
  methods: {
    getData() {
      let params = new URLSearchParams();
      params.append("openid", localStorage.getItem("openid"));
      params.append("p", 1);
      axios.post(url.myPoints, params).then(res => {
        this.list = res.data.data;
      });
    }
  },
  filters: {
    formate: function(value) {
      let a = new Date(value);
      let b = a.toLocaleString();
      return b;
    }
  }
};
</script>


<style scoped lang="scss">
.myPoints {
  padding: 10px;
  header {
    background: url(../../assets/images/integral_bg@2x.png) no-repeat;
    background-size: 100% 100%;
    height: 175px;
    color: #fff;
    padding: 20px;
    h1 {
      font-family: SourceHanSansCN-Regular;
      font-size: 45px;
      margin-bottom: 10px;
    }
    p {
      font-size: 13px;
    }
  }
  main {
    padding: 10px;
    h2 {
      margin: 10px 0;
      font-size: 14px;
    }
    ul {
      li {
        display: flex;
        align-items: center;
        margin-bottom: 20px;
        .left {
          h1 {
            font-size: 15px;
            font-family: SourceHanSansCN-Regular;
            font-weight: 400;
          }
          p {
            font-size: 11px;
            font-family: SourceHanSansCN-Regular;
            font-weight: 400;
            color: rgba(164, 164, 164, 1);
            margin-top: 5px;
          }
        }
        .right {
          margin-left: auto;
          font-size: 17px;
          font-family: SourceHanSansCN-Medium;
          font-weight: 500;
          color: rgba(236, 174, 51, 1);
        }
      }
    }
  }
}
</style>



